.nav {
  /* background-color: #edf2f8; */
  
  background-color: rgb(230, 245, 230);
  height: 70px;
  width: 100%;
  position: fixed;
  z-index: 99;
  opacity: 0.9;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav ul li {
  display: inline-block; /*making list horizontaly alligned */
  margin: 20px;
}
.nav ul li a {
  text-decoration: none;
  color: black;
  font-weight: 650;
}
.nav ul li a:hover {
  color: rgb(235, 35, 35);
  border-bottom: 3px solid rgb(235, 35, 35);
}
